import { Injectable } from '@angular/core';
@Injectable({
  providedIn: 'root',
})
export class InformationService {
  private petId!: number;
  private postId!: number;

  constructor() {}

  setPetId(petId: number) {
    this.petId = petId;
  }
  getPetId() {
    return this.petId;
  }

  setPostId(postId: number) {
    this.postId = postId;
  }
  getPostId() {
    return this.postId;
  }
  getuser(): number {
    const userId = localStorage.getItem('user_id');
    if (userId) {
      return parseInt(userId);
    }
    return 0;
  }
  getType(): string {
    const type = localStorage.getItem('type');
    if (type) {
      return type;
    }
    return '';
  }
  nameVet(): string {
    const name = localStorage.getItem('vet_name');
    if (name) {
      return name;
    }
    return '';
  }
}
