import { Component, OnInit } from '@angular/core';
import { MatDialog } from '@angular/material/dialog';
import { UserInfo } from 'src/app/models/user.dto';
import { InformationService } from 'src/app/services/information-service.service';
import { UserService } from 'src/app/services/user.service';
import { RegisterComponent } from '../register/register.component';

@Component({
  selector: 'app-info-user',
  templateUrl: './info-user.component.html',
  styleUrls: ['./info-user.component.css'],
})
export class InfoUserComponent implements OnInit {
  user!: UserInfo;

  constructor(
    private userService: UserService,
    private info: InformationService,
    private dialog: MatDialog
  ) {}

  ngOnInit() {
    this.load();
  }

  load(): void {
    this.userService.getUser(this.info.getuser()).subscribe((r) => {
      this.user = r;
    });
  }

  openReg() {
    let d = this.dialog.open(RegisterComponent, { width: '70%' });
    d.afterClosed().subscribe(() => this.load());
  }
}
