import { Component, OnInit } from '@angular/core';
import { Veterinarian } from 'src/app/models/veterinarian';
import { InformationService } from 'src/app/services/information-service.service';
import { MessageService } from 'src/app/services/message.service';
import { VeterinaryService } from 'src/app/services/veterinary.service';

@Component({
  selector: 'app-vet-list',
  templateUrl: './vet-list.component.html',
  styleUrls: ['./vet-list.component.css'],
})
export class VetListComponent implements OnInit {
  vet!: Veterinarian[];
  idCenter: number = -1;
  constructor(
    private vetService: VeterinaryService,
    private info: InformationService,
    private message: MessageService
  ) {}

  ngOnInit(): void {
    this.vetService.getbyID(this.info.getuser()).subscribe((r) => {
      if (r.center_id) {
        this.idCenter = r.center_id;
        if (this.idCenter != -1) {
          this.loadInfo();
        }
      }
    });
  }

  loadInfo(): void {
    this.vetService.getVets().subscribe((d) => {
      this.vet = d.filter(
        (vet) =>
          vet.center_id?.toString() === this.idCenter.toString() &&
          vet.vet_status === false
      );
    });
  }

  unir(id: number) {
    this.vetService.aceptJoin(id).subscribe((r) => {
      if (r.status) this.message.msg('Veterinario aadido al centro', 'Cerrar');
      this.loadInfo();
    });
  }
}
