import { Component, Optional } from '@angular/core';
import {
  FormBuilder,
  FormControl,
  FormGroup,
  Validators,
} from '@angular/forms';
import { MatDialogRef } from '@angular/material/dialog';
import { Activity } from 'src/app/models/activity';
import { ActivityService } from 'src/app/services/activity.service';
import { InformationService } from 'src/app/services/information-service.service';
import { MessageService } from 'src/app/services/message.service';

@Component({
  selector: 'app-activity-new',
  templateUrl: './activity-new.component.html',
  styleUrls: ['./activity-new.component.css'],
})
export class ActivityNewComponent {
  isUpdateMode: boolean;
  act: Activity;
  activity_name: FormControl;
  activity_type: FormControl;
  activity_time_start: FormControl;
  activity_minutes: FormControl;
  activity_comments: FormControl;
  actForm: FormGroup;
  valid: boolean | null;
  tipos: string[];

  constructor(
    private formBuilder: FormBuilder,
    private actService: ActivityService,
    private info: InformationService,
    private messageService: MessageService,
    @Optional() public dialogRef: MatDialogRef<ActivityNewComponent>
  ) {
    this.isUpdateMode = false;
    this.act = new Activity('', '', '', '', '', -1);

    this.valid = null;
    this.activity_name = new FormControl(this.act.activity_name, [
      Validators.required,
    ]);
    this.activity_type = new FormControl(this.act.activity_type, [
      Validators.required,
    ]);
    this.activity_time_start = new FormControl(this.act.activity_time_start, [
      Validators.required,
    ]);
    this.activity_time_start.setValue([new Date()]);
    this.activity_minutes = new FormControl(this.act.activity_minutes);
    this.activity_comments = new FormControl(this.act.activity_comments, [
      Validators.required,
    ]);

    this.actForm = this.formBuilder.group({
      activity_name: this.activity_name,
      activity_type: this.activity_type,
      activity_time_start: this.activity_time_start,
      activity_minutes: this.activity_minutes,
      activity_comments: this.activity_comments,
      pet_id: -1,
    });

    this.activity_time_start.setValue(new Date().toISOString().split('T')[0]);

    this.tipos = this.actService.getTipos();
  }

  async save(): Promise<void> {
    this.valid = false;
    if (this.actForm.invalid) {
      return;
    }
    this.valid = true;
    this.act = this.actForm.value;
    const id = this.info.getPetId();
    this.act.pet_id = id;
    console.log(this.act);

    this.actService.new(this.act).subscribe((data) => {
      if (data.status === true)
        this.messageService.msg('Actividad aadida.', 'Cerrar');
      else {
        this.messageService.msg(
          'Error al aadir, intentalo de nuevo.',
          'Cerrar'
        );
      }
    });
    this.dialogRef.close('submit');
  }
}
