import { Component, Input, OnInit } from '@angular/core';
import { MatDialog } from '@angular/material/dialog';
import { Center } from 'src/app/models/center';
import { CenterService } from 'src/app/services/center.service';
import { JoinCenterComponent } from '../join-center/join-center.component';
@Component({
  selector: 'app-center-info',
  templateUrl: './center-info.component.html',
  styleUrls: ['./center-info.component.css'],
})
export class CenterInfoComponent implements OnInit {
  center!: Center;
  @Input() id!: string;
  
  constructor(
    private centerService: CenterService,
    private dialog: MatDialog
  ) {}

  ngOnInit() {
    this.load();
  }
  load(): void {
    if (this.id)
      this.centerService.getbyID(parseInt(this.id)).subscribe((d) => {
        this.center = d;
      });
  }

  selectCenter() {
    let diag = this.dialog.open(JoinCenterComponent, {
      width: '70%',
      panelClass: 'dialog-button',
    });
    diag.afterClosed().subscribe((e) => {
      if (e != undefined) {
        this.id = e;
        this.load();
      }
    });
  }
}
