import { Component, OnInit } from '@angular/core';
import {
  FormBuilder,
  FormControl,
  FormGroup,
  Validators,
} from '@angular/forms';
import { Router } from '@angular/router';
import { LogClass } from 'src/app/models/login.dto';
import { MessageService } from 'src/app/services/message.service';
import { UserService } from 'src/app/services/user.service';
import { VeterinaryService } from 'src/app/services/veterinary.service';

@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.css'],
})
export class LoginComponent implements OnInit {
  valid: boolean | null;
  loginUser: LogClass;
  email: FormControl;
  password: FormControl;
  loginForm: FormGroup;
  tipo: FormControl;
  constructor(
    private formBuilder: FormBuilder,
    private router: Router,
    private userService: UserService,
    private vetService: VeterinaryService,
    private messageService: MessageService
  ) {
    this.loginUser = new LogClass('', '', '', '');
    this.valid = null;
    this.email = new FormControl('', [
      Validators.required,
      Validators.pattern('[a-z0-9._%+-]+@[a-z0-9.-]+.[a-z]{2,4}$'),
    ]);
    this.tipo = new FormControl('', [Validators.required]);
    this.password = new FormControl('', [
      Validators.required,
      Validators.minLength(8),
      Validators.maxLength(16),
    ]);

    this.loginForm = this.formBuilder.group({
      email: this.email,
      password: this.password,
    });
  }

  ngOnInit(): void {}

  async login(): Promise<void> {
    this.loginUser.email = this.email.value;
    this.loginUser.password = this.password.value;

    if (this.tipo.value == '2') {
      this.logVet();
    } else {
      this.logUser();
    }
  }

  logUser() {
    this.userService.login(this.loginUser).subscribe((data) => {
      if (data.status == true) {
        localStorage.setItem('token', data.token);
        localStorage.setItem('user_id', data.id.toString());
        localStorage.setItem('type', 'user');
        this.userService.authChange.emit(true);
        this.router.navigateByUrl('init');
      } else {
        this.messageService.msg(data.response, 'Cerrar');
      }
    });
  }

  logVet() {
    this.vetService.login(this.loginUser).subscribe((data) => {
      if (data.status == true) {
        localStorage.setItem('token', data.token);
        localStorage.setItem('user_id', data.id.toString());
        localStorage.setItem('type', 'vet');
        this.vetService.authChange.emit(true);
        this.router.navigateByUrl('init');
      } else {
        this.messageService.msg(data.response, 'Cerrar');
      }
    });
  }
}
