import { Component, Optional } from '@angular/core';
import {
  FormBuilder,
  FormControl,
  FormGroup,
  Validators,
} from '@angular/forms';
import { MatDialogRef } from '@angular/material/dialog';
import { Reminder } from 'src/app/models/reminder';
import { InformationService } from 'src/app/services/information-service.service';
import { MessageService } from 'src/app/services/message.service';
import { ReminderService } from 'src/app/services/reminder.service';

@Component({
  selector: 'app-reminders-new',
  templateUrl: './reminders-new.component.html',
  styleUrls: ['./reminders-new.component.css'],
})
export class RemindersNewComponent {
  hours: string[] = [];
  minutes: string[] = [];

  reminder: Reminder;
  hide = true;
  reminder_title: FormControl;
  reminder_date: FormControl;
  reminder_comments: FormControl;
  r_hora: FormControl;
  r_min: FormControl;

  reminderForm: FormGroup;
  valid: boolean | null;
  isUpdateMode: boolean = false;

  constructor(
    private formBuilder: FormBuilder,
    private reminderService: ReminderService,
    private messageService: MessageService,
    private info: InformationService,
    @Optional() public dialogRef: MatDialogRef<RemindersNewComponent>
  ) {
    this.reminder = new Reminder('', new Date(), '', -1);
    this.valid = null;
    this.reminder_title = new FormControl(this.reminder.reminder_title, [
      Validators.required,
    ]);
    this.reminder_date = new FormControl(this.reminder.reminder_date, [
      Validators.required,
    ]);
    this.r_hora = new FormControl([
      Validators.required,
      Validators.min(0),
      Validators.max(23),
    ]);
    this.r_min = new FormControl([
      Validators.required,
      Validators.min(0),
      Validators.max(59),
    ]);
    this.reminder_comments = new FormControl(this.reminder.reminder_comments);

    this.reminderForm = this.formBuilder.group({
      reminder_title: this.reminder_title,
      reminder_comments: this.reminder_comments,
      user_id: this.info.getuser(),
      reminder_date: this.reminder_date,
    });
    this.reminder_date.setValue(new Date().toISOString().split('T')[0]);
    this.cargarHorasmin();
  }

  cargarHorasmin() {
    for (let i = 0; i < 24; i++) {
      let n = i < 10 ? '0' + i : '' + i;
      this.hours.push(n);
    }
    for (let i = 0; i < 60; i++) {
      let n = i < 10 ? '0' + i : '' + i;
      this.minutes.push(n);
    }
  }
  async save() {
    if (this.reminderForm.invalid) {
      return;
    }
    this.reminder = this.reminderForm.value;
    const data = new Date(this.reminder.reminder_date);

    this.reminder.reminder_date = new Date(
      Date.UTC(
        data.getFullYear(),
        data.getUTCMonth() + 1,
        data.getUTCDate(),
        this.r_hora.value,
        this.r_min.value
      )
    );
    this.reminderService.new(this.reminder).subscribe((d) => {
      if (d.status) this.messageService.msg('Creado correctamente.', 'cerrar');
      else this.messageService.msg('Error al crear el recordatorio.', 'Cerrar');
    });
    this.dialogRef.close();
  }
}
