import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { Reminder } from '../models/reminder';
import { ResponseAPI } from '../models/responseAPI';

@Injectable({
  providedIn: 'root',
})
export class ReminderService {
  private URL: string;

  constructor(private http: HttpClient) {
    this.URL = 'http://54.216.130.53/reminder/';
  }

  new(rem: Reminder): Observable<ResponseAPI> {
    return this.http.post<ResponseAPI>(this.URL, rem);
  }

  getAll(id: number): Observable<Reminder[]> {
    return this.http.get<Reminder[]>(this.URL + '?user_id=' + id);
  }
  updateVet(id: number, rem: Reminder): Observable<ResponseAPI> {
    return this.http.patch<ResponseAPI>(this.URL + id, rem);
  }
  remove(id: number): Observable<ResponseAPI> {
    return this.http.delete<ResponseAPI>(this.URL + '?reminder_id=' + id);
  }
}
