export class MessageVet {
  message_id!: number;
  message_body: string;
  message_title: string;
  pet_id: number;
  vet_id: number;
  center_id: number;
  creation: Date;
  message_response!: string;

  constructor(
    message_body: string,
    message_title: string,
    pet_id: number,
    vet_id: number,
    center_id: number,
    creation: Date
  ) {
    this.message_body = message_body;
    this.message_title = message_title;
    this.pet_id = pet_id;
    this.vet_id = vet_id;
    this.center_id = center_id;
    this.creation = creation;
  }
}
