import { Component, Input } from '@angular/core';
import { MatDialog } from '@angular/material/dialog';
import { Activity } from 'src/app/models/activity';
import { ActivityService } from 'src/app/services/activity.service';
import { InformationService } from 'src/app/services/information-service.service';
import { ActivityNewComponent } from '../activity-new/activity-new.component';

@Component({
  selector: 'app-activity-list',
  templateUrl: './activity-list.component.html',
  styleUrls: ['./activity-list.component.css'],
})
export class ActivityListComponent {
  activities!: Activity[];
  @Input() petId!: string;
  canAdd: boolean = true;
  constructor(
    private activityService: ActivityService,
    private dialog: MatDialog,
    private info: InformationService
  ) {
    if (this.info.getType() == 'vet') this.canAdd = false;
  }

  ngOnInit(): void {
    this.load();
  }

  load(): void {
    this.activityService
      .getActivities(parseInt(this.petId))
      .subscribe((data) => {
        if (data.length > 0) this.activities = data;
      });
  }

  newActivity(): void {
    let dialogRef = this.dialog.open(ActivityNewComponent, {
      width: '70%',
      panelClass: 'dialog-button',
    });
    dialogRef.afterClosed().subscribe(() => {
      this.load();
    });
  }
}
