import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { PetInfo } from 'src/app/models/pet.dto';
import { InformationService } from 'src/app/services/information-service.service';
import { MessageService } from 'src/app/services/message.service';
import { PetService } from 'src/app/services/pet.service';
@Component({
  selector: 'app-pet-details',
  templateUrl: './pet-details.component.html',
  styleUrls: ['./pet-details.component.css'],
})
export class PetDetailsComponent implements OnInit {
  pet!: PetInfo;
  petId: number;
  centerid!: number;

  constructor(
    private petService: PetService,
    private route: ActivatedRoute,
    private messageService: MessageService,
    private infoService: InformationService
  ) {
    this.petId = -1;
  }

  ngOnInit() {
    this.route.params.subscribe((params) => {
      this.petId = params['petId'];
      this.infoService.setPetId(this.petId);

      this.petService.getPetbyID(this.petId).subscribe((response) => {
        if (response) {
          this.pet = response;
        } else {
          this.messageService.msg(
            'Error al obtener los datos de la mascota.',
            'Cerrar'
          );
        }
      });
    });
  }

  async delete(petId: number) {
    this.petService.delete(petId, this.infoService.getuser()).subscribe((r) => {
      if (r.status) {
        this.messageService.msg('Mascota eliminada.', 'Cerrar');
      }
    });
  }
}
