import { Component, OnInit, Optional } from '@angular/core';
import {
  FormBuilder,
  FormControl,
  FormGroup,
  Validators,
} from '@angular/forms';
import { MatDialogRef } from '@angular/material/dialog';
import { Post } from 'src/app/models/post';
import { InformationService } from 'src/app/services/information-service.service';
import { MessageService } from 'src/app/services/message.service';
import { PostService } from 'src/app/services/post.service';
import { VeterinaryService } from 'src/app/services/veterinary.service';

@Component({
  selector: 'app-post-new',
  templateUrl: './post-new.component.html',
  styleUrls: ['./post-new.component.css'],
})
export class PostNewComponent implements OnInit {
  animalTypes: string[];

  categories: string[];

  isUpdateMode: boolean;
  post: Post;
  post_title: FormControl;
  post_body: FormControl;
  post_category: FormControl;
  post_type_animal: FormControl;
  post_img: FormControl;

  name: string = '';
  postForm: FormGroup;
  valid: boolean | null;

  constructor(
    private formBuilder: FormBuilder,
    private postService: PostService,
    private messageService: MessageService,
    private info: InformationService,
    private vetService: VeterinaryService,
    @Optional() public dialogRef: MatDialogRef<PostNewComponent>
  ) {
    this.isUpdateMode = false;
    this.post = new Post('', '', -1, '', '', '');
    this.categories = this.postService.getCategories();
    this.animalTypes = this.postService.getAnimalTypes();

    this.valid = null;
    this.post_title = new FormControl(this.post.post_title, [
      Validators.required,
    ]);
    this.post_body = new FormControl(this.post.post_body, [
      Validators.required,
    ]);
    this.post_type_animal = new FormControl(this.post.post_type_animal, [
      Validators.required,
    ]);
    this.post_category = new FormControl(this.post.post_category, [
      Validators.required,
    ]);
    this.post_img = new FormControl(this.post.post_img, [Validators.required]);

    this.postForm = this.formBuilder.group({
      post_title: this.post_title,
      post_body: this.post_body,
      post_category: this.post_category,
      post_type_animal: this.post_type_animal,
      vet_id: -1,
      post_author: '',
      post_img: this.post_img,
    });
  }
  ngOnInit() {
    this.vetService.getbyID(this.info.getuser()).subscribe((V) => {
      this.name = V.vet_name;
    });
    const postId = this.info.getPostId();
    if (postId > -1) {
      this.isUpdateMode = true;
      this.postService.getPostbyID(postId).subscribe((d) => {
        this.post = d;
        this.post_title.setValue(this.post.post_title);
        this.post_body.setValue(this.post.post_body);
        this.post_type_animal.setValue(this.post.post_type_animal);
        this.post_category.setValue(this.post.post_category);
        this.post_img.setValue(this.post.post_img);
        this.postForm = this.formBuilder.group({
          post_title: this.post_title,
          post_body: this.post_body,
          post_category: this.post_category,
          post_type_animal: this.post_type_animal,
          vet_id: this.info.getuser(),
          post_author: this.info.nameVet(),
          post_img: this.post.post_img,
        });
      });
    }
  }

  async save(): Promise<void> {
    this.valid = false;
    if (this.postForm.invalid) {
      return;
    }
    this.valid = true;
    this.post = this.postForm.value;

    this.post.vet_id = this.info.getuser();
    if (this.isUpdateMode) {
      this.update();
    } else {
      this.post.post_author = this.name;
      this.new();
    }
  }

  private update() {
    this.postService
      .update(this.info.getPostId(), this.post)
      .subscribe((data) => {
        if (data.status === true)
          this.messageService.msg('Post actualizado.', 'Cerrar');
        else {
          this.messageService.msg(
            'Error al actualizar, intentalo de nuevo.',
            'Cerrar'
          );
        }
      });
    this.dialogRef.close(true);
  }

  private new() {
    this.postService.new(this.post).subscribe((data) => {
      if (data.status === true)
        this.messageService.msg('Post creado.', 'Cerrar');
      else {
        this.messageService.msg(
          'Error al aadir, intentalo de nuevo.',
          'Cerrar'
        );
      }
    });
    this.dialogRef.close(true);
  }
}
