import { Component, OnInit } from '@angular/core';
import { MatDialog } from '@angular/material/dialog';
import { MatTableDataSource } from '@angular/material/table';
import { Router } from '@angular/router';
import { CenterService } from 'src/app/services/center.service';
import { CenterRegisterComponent } from '../center-register/center-register.component';

export interface PeriodicElement {
  center_id: number;
  center_name: string;
  center_street: string;
  center_valoration: number;
}

@Component({
  selector: 'app-center-main',
  templateUrl: './center-main.component.html',
  styleUrls: ['./center-main.component.css'],
})
export class CenterMainComponent implements OnInit {
  displayedColumns: string[] = [
    'center_id',
    'center_name',
    'center_street',
    'center_valoration',
  ];
  dataSource: any;
  clickedRows = new Set<PeriodicElement>();

  constructor(
    private centerService: CenterService,
    private route: Router,
    private dialog: MatDialog
  ) {}

  ngOnInit(): void {
    this.getAll();
  }

  getAll(): void {
    this.centerService.getAll().subscribe((d) => {
      this.dataSource = new MatTableDataSource(d);
    });
  }

  viewDetails(a: any) {
    this.route.navigateByUrl('center/' + a.center_id);
  }

  applyFilter(event: Event) {
    const filterValue = (event.target as HTMLInputElement).value;
    this.dataSource.filter = filterValue.trim().toLowerCase();
  }

  new() {
    const diagref = this.dialog.open(CenterRegisterComponent, {
      width: '70%',
      data: { centerId: -1 },
    });
    diagref.afterClosed().subscribe(() => this.getAll());
  }
}
