import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { Post } from '../models/post';
import { ResponseAPI } from '../models/responseAPI';

@Injectable({
  providedIn: 'root',
})
export class PostService {
  private URL: string;
  animalTypes: string[] = [
    'Perros',
    'Gatos',
    'Aves',
    'Roedores',
    'Reptiles',
    'Peces',
    'Animales exticos',
    'Otros',
  ];
  categories: string[] = [
    'Consejos de salud animal',
    'Tratamientos y procedimientos veterinarios',
    'Historias de casos clnicos',
    'Eventos y actividades comunitarias',
    'Novedades en medicina veterinaria',
    'Consejos de comportamiento animal',
    'Cuidado preventivo y vacunacin',
    'Noticias locales relacionadas con animales',
  ];

  constructor(private http: HttpClient) {
    this.URL = 'http://54.216.130.53/post/';
  }

  getCategories(): string[] {
    return this.categories;
  }
  getAnimalTypes(): string[] {
    return this.animalTypes;
  }

  getPost(): Observable<Post[]> {
    return this.http.get<Post[]>(this.URL);
  }

  new(post: Post): Observable<ResponseAPI> {
    return this.http.post<ResponseAPI>(this.URL, post);
  }
  getPostbyID(post_id: number): Observable<Post> {
    return this.http.get<Post>(this.URL + post_id);
  }
  remove(post_id: number): Observable<ResponseAPI> {
    return this.http.delete<ResponseAPI>(this.URL + '?post_id=' + post_id);
  }
  update(post_id: number, post: Post): Observable<ResponseAPI> {
    return this.http.patch<ResponseAPI>(this.URL + post_id, post);
  }
  getMyPost(vet_id: number): Observable<Post[]> {
    return this.http.get<Post[]>(this.URL + 'my/' + vet_id);
  }
  valoration(post_id: number, votes: number): Observable<ResponseAPI> {
    return this.http.patch<ResponseAPI>(
      this.URL + 'val/' + post_id + '?votes=' + votes,
      ''
    );
  }
}
