import { Component, OnInit } from '@angular/core';
import { MatDialog } from '@angular/material/dialog';
import { Router } from '@angular/router';
import { Post } from 'src/app/models/post';
import { InformationService } from 'src/app/services/information-service.service';
import { PostService } from 'src/app/services/post.service';
import { PostNewComponent } from '../post-new/post-new.component';

@Component({
  selector: 'app-post-list',
  templateUrl: './post-list.component.html',
  styleUrls: ['./post-list.component.css'],
})
export class PostListComponent implements OnInit {
  posts!: Post[];
  fposts!: Post[];

  canAdd: Boolean = false;
  selectedCategory: string = '';
  categories: string[] = [];
  selectedAnimalType: string = '';
  animalTypes: string[] = [];
  constructor(
    private postService: PostService,
    public dialog: MatDialog,
    private info: InformationService,
    private router: Router
  ) {}

  ngOnInit(): void {
    this.getPost();
    this.info.setPostId(-1);
    this.categories = this.postService.getCategories();
    this.animalTypes = this.postService.getAnimalTypes();
    if (this.info.getType() == 'vet') {
      this.canAdd = true;
    }
  }

  private async getPost(): Promise<void> {
    this.postService.getPost().subscribe((data) => {
      if (data.length > 0) {
        this.posts = data;
        this.fposts = this.posts;
      }
    });
  }

  registerPost(): void {
    let d = this.dialog.open(PostNewComponent, { width: '70%' });
    d.afterClosed().subscribe(() => this.getPost());
  }
  filterPosts(): void {
    this.fposts = this.posts.filter((post) => {
      return (
        (this.selectedCategory
          ? post.post_category === this.selectedCategory
          : true) &&
        (this.selectedAnimalType
          ? post.post_type_animal === this.selectedAnimalType
          : true)
      );
    });
  }

  viewPost(id: number) {
    this.info.setPostId(id);
    this.router.navigateByUrl('post/' + id);
  }
}
