export class Post {
  post_body: string;
  post_category: string;
  post_id!: number;
  vet_id: number;
  post_author: string;
  creation!: Date;
  modify!: Date;
  post_type_animal: string;
  post_title: string;
  valoration!: number;
  post_img!: string;

  constructor(
    post_body: string,
    post_category: string,
    vet_id: number,
    post_type_animal: string,
    post_title: string,
    post_author: string
  ) {
    this.post_body = post_body;
    this.post_category = post_category;
    this.vet_id = vet_id;
    this.post_type_animal = post_type_animal;
    this.post_title = post_title;
    this.post_author = post_author;
  }
}
