import { Component, OnInit, Optional } from '@angular/core';
import {
  FormBuilder,
  FormControl,
  FormGroup,
  Validators,
} from '@angular/forms';
import { MatDialogRef } from '@angular/material/dialog';
import { Center } from 'src/app/models/center';
import { CenterService } from 'src/app/services/center.service';
import { InformationService } from 'src/app/services/information-service.service';
import { MessageService } from 'src/app/services/message.service';
import { PetService } from 'src/app/services/pet.service';

@Component({
  selector: 'app-join-center',
  templateUrl: './join-center.component.html',
  styleUrls: ['./join-center.component.css'],
})
export class JoinCenterComponent implements OnInit {
  centerForm: FormGroup;
  center!: Center[];
  center_id: FormControl;
  valid: boolean | null;

  constructor(
    private formBuilder: FormBuilder,
    private centerService: CenterService,
    private petService: PetService,
    private info: InformationService,
    private messageService: MessageService,
    @Optional() public dialogRef: MatDialogRef<JoinCenterComponent>
  ) {
    this.valid = null;

    this.center_id = new FormControl('', [Validators.required]);

    this.centerForm = this.formBuilder.group({
      center_id: this.center_id,
    });
  }

  ngOnInit() {
    this.centerService.getAll().subscribe((d) => {
      this.center = d;
    });
  }
  save() {
    this.valid = false;
    if (this.centerForm.invalid) {
      return;
    }
    this.valid = true;
    this.petService
      .updatePetCenter(this.info.getPetId(), this.center_id.value)
      .subscribe((r) => {
        if (r.status === true) {
          this.messageService.msg('Centro actualizado.', 'Cerrar');
        } else {
          this.messageService.msg(
            'Error al enviar unirse al centro, intentalo de nuevo.',
            'Cerrar'
          );
        }
      });
    this.dialogRef.close(this.center_id.value);
  }
}
