import { Component, OnInit } from '@angular/core';
import { MatDialog } from '@angular/material/dialog';
import { Reminder } from 'src/app/models/reminder';
import { InformationService } from 'src/app/services/information-service.service';
import { ReminderService } from 'src/app/services/reminder.service';
import { RemindersNewComponent } from '../reminders-new/reminders-new.component';

@Component({
  selector: 'app-reminders-list',
  templateUrl: './reminders-list.component.html',
  styleUrls: ['./reminders-list.component.css'],
})
export class RemindersListComponent implements OnInit {
  reminders!: Reminder[];
  constructor(
    private reminderService: ReminderService,
    private info: InformationService,
    private dialog: MatDialog
  ) {}
  ngOnInit() {
    this.load();
  }

  load(): void {
    this.reminderService.getAll(this.info.getuser()).subscribe((r) => {
      this.reminders = r
        .filter((rem) => new Date(rem.reminder_date) > new Date())
        .map((rem) => ({
          ...rem,
          reminder_date: new Date(rem.reminder_date),
        }));
    });
  }

  new() {
    let d = this.dialog.open(RemindersNewComponent, { width: '70%' });
    d.afterClosed().subscribe(() => {
      this.load();
    });
  }

  delete(id: number) {
    this.reminderService.remove(id).subscribe((r) => {
      if (r.status) {
        this.load();
      }
    });
  }
}
