export class Center {
  center_id!: number;
  center_name: string;
  center_street: string;
  center_information: string;
  center_logo: string;
  email_admin: string;
  center_valoration!: number;
  center_nif: string;
  center_phone!: string;

  constructor(
    center_name: string,
    center_street: string,
    center_information: string,
    center_logo: string,
    center_nif: string,
    email_admin: string
  ) {
    this.center_name = center_name;
    this.center_street = center_street;
    this.center_information = center_information;
    this.center_logo = center_logo;
    this.email_admin = email_admin;
    this.center_nif = center_nif;
  }
}
