import { Component } from '@angular/core';
import { InfoUserComponent } from '../users/info-user/info-user.component';
import { InformationService } from 'src/app/services/information-service.service';

@Component({
  selector: 'app-principal',
  templateUrl: './principal.component.html',
  styleUrls: ['./principal.component.css']
})
export class PrincipalComponent {

  isUser: Boolean = true;
  constructor(private info: InformationService){}

  ngOnInit(){
    if(this.info.getType()=='vet'){
      this.isUser=false;
    }
  }
}
