export class Activity {
  activity_id!: number;
  activity_name: string;
  activity_type: string;
  activity_time_start: Date;
  activity_time_end: Date;
  activity_minutes!: string;
  activity_comments: string;
  pet_id: number;

  constructor(
    activity_name: string,
    activity_type: string,
    activity_time_start: string,
    activity_time_end: string,
    activity_comments: string,
    pet_id: number
  ) {
    this.activity_name = activity_name;
    this.activity_type = activity_type;
    this.activity_time_start = new Date(activity_time_start);
    this.activity_time_end = new Date(activity_time_end);
    this.activity_comments = activity_comments;
    this.pet_id = pet_id;
  }
}
