import { Component, OnInit } from '@angular/core';
import { MatDialog } from '@angular/material/dialog';
import { Veterinarian } from 'src/app/models/veterinarian';
import { InformationService } from 'src/app/services/information-service.service';
import { VeterinaryService } from 'src/app/services/veterinary.service';
import { RegisterVetComponent } from '../register-vet/register-vet.component';

@Component({
  selector: 'app-vet-info',
  templateUrl: './vet-info.component.html',
  styleUrls: ['./vet-info.component.css'],
})
export class VetInfoComponent implements OnInit {
  Vet!: Veterinarian;

  constructor(
    private vetService: VeterinaryService,
    private dialog: MatDialog,
    private info: InformationService
  ) {}

  ngOnInit(): void {
    this.load();
  }
  load(): void {
    this.vetService.getbyID(this.info.getuser()).subscribe((d) => {
      this.Vet = d;
    });
  }

  openReg() {
    let d = this.dialog.open(RegisterVetComponent, { width: '70%' });
    d.afterClosed().subscribe(() => this.load());
  }
}
