import { Component, OnInit } from '@angular/core';
import { MatDialog } from '@angular/material/dialog';
import { Router } from '@angular/router';
import { PetInfo } from 'src/app/models/pet.dto';
import { InformationService } from 'src/app/services/information-service.service';
import { PetService } from 'src/app/services/pet.service';
import { PetRegisterComponent } from '../pet-register/pet-register.component';

@Component({
  selector: 'app-pet-list',
  templateUrl: './pet-list.component.html',
  styleUrls: ['./pet-list.component.css'],
})
export class PetListComponent implements OnInit {
  pets!: PetInfo[];
  canAdd: boolean = true;
  constructor(
    private petservice: PetService,
    private router: Router,
    public dialog: MatDialog,
    private info: InformationService
  ) {
    this.info.setPetId(-1);
  }

  ngOnInit(): void {
    if (this.info.getType() == 'vet') {
      this.canAdd = false;
      this.getPetVets();
    } else {
      this.getPets();
    }
  }

  private async getPets(): Promise<void> {
    const userId = this.info.getuser();
    if (userId) {
      this.petservice.getPets(userId).subscribe((data) => {
        if (data.length > 0) {
          this.pets = data;
        }
      });
    }
  }
  private async getPetVets(): Promise<void> {
    this.petservice.getPetsCenter(this.info.getuser()).subscribe((data) => {
      if (data.length > 0) {
        this.pets = data;
      }
    });
  }

  registerPets(): void {
    let d = this.dialog.open(PetRegisterComponent, { width: '70%' });
    d.afterClosed().subscribe(() => this.getPets());
  }

  petInfo(petId: number): void {
    this.info.setPetId(petId);
    this.router.navigateByUrl('pets-info/' + petId);
  }
}
