export class Reminder {
    reminder_id!: number;
    reminder_title: string;
    reminder_date: Date;
    reminder_comments: string;
    user_id: number;
  
    constructor(
      reminder_title: string,
      reminder_date: Date,
      reminder_comments: string,
      user_id: number
    ) {
      this.reminder_title = reminder_title;
      this.reminder_date = reminder_date;
      this.reminder_comments = reminder_comments;
      this.user_id = user_id;
    }
  }
  