import { Component, Inject, OnInit, Optional } from '@angular/core';
import {
  FormBuilder,
  FormControl,
  FormGroup,
  Validators,
} from '@angular/forms';
import { MAT_DIALOG_DATA, MatDialogRef } from '@angular/material/dialog';
import { Center } from 'src/app/models/center';
import { CenterService } from 'src/app/services/center.service';
import { MessageService } from 'src/app/services/message.service';

@Component({
  selector: 'app-center-register',
  templateUrl: './center-register.component.html',
  styleUrls: ['./center-register.component.css'],
})
export class CenterRegisterComponent implements OnInit {
  isUpdateMode: boolean;
  center: Center;
  center_name: FormControl;
  center_street: FormControl;
  center_information: FormControl;
  center_logo: FormControl;
  email_admin: FormControl;
  center_nif: FormControl;
  center_phone: FormControl;
  registerCenter: FormGroup;
  valid: boolean | null;
  idCenter: number = -1;
  constructor(
    private formBuilder: FormBuilder,
    private centerService: CenterService,
    private messageService: MessageService,
    @Inject(MAT_DIALOG_DATA) public data: any,
    @Optional() public dialogRef: MatDialogRef<CenterRegisterComponent>
  ) {
    this.isUpdateMode = false;
    this.center = new Center('', '', '', '', '', '');

    this.valid = null;
    this.center_name = new FormControl(this.center.center_name, [
      Validators.required,
    ]);
    this.center_street = new FormControl(this.center.center_street);
    this.center_information = new FormControl(this.center.center_information);
    this.center_logo = new FormControl(this.center.center_logo);
    this.email_admin = new FormControl(this.center.email_admin, [
      Validators.email,
    ]);
    this.center_nif = new FormControl(this.center.center_nif, [
      Validators.required,
    ]);
    this.center_phone = new FormControl(this.center.center_phone, [
      Validators.required,
    ]);
    this.registerCenter = this.formBuilder.group({
      center_name: this.center_name,
      center_street: this.center_street,
      center_information: this.center_information,
      center_logo: this.center_logo,
      email_admin: this.email_admin,
      center_nif: this.center_nif,
      center_phone: this.center_phone,
    });
    this.idCenter = this.data.centerId;
  }
  ngOnInit() {
    if (this.idCenter > -1) {
      this.isUpdateMode = true;
      this.centerService.getbyID(this.idCenter).subscribe((d) => {
        this.center = d;

        this.center_name.setValue(this.center.center_name);
        this.center_street.setValue(this.center.center_street);
        this.center_information.setValue(this.center.center_information);
        this.center_logo.setValue(this.center.center_logo);
        this.email_admin.setValue(this.center.email_admin);
        this.center_nif.setValue(this.center.center_nif);
        this.center_phone.setValue(this.center.center_phone);

        this.registerCenter = this.formBuilder.group({
          center_name: this.center_name,
          center_street: this.center_street,
          center_information: this.center_information,
          center_logo: this.center_logo,
          email_admin: this.email_admin,
          center_phone: this.center_phone,
          center_nif: this.center_nif,
        });
      });
    }
  }

  async save(): Promise<void> {
    this.valid = false;
    if (this.registerCenter.invalid) {
      return;
    }
    this.valid = true;
    this.center = this.registerCenter.value;

    if (this.isUpdateMode) {
      this.update();
    } else {
      this.register();
    }
  }

  private update() {
    this.centerService
      .updateCenter(this.idCenter, this.center)
      .subscribe((data) => {
        if (data.status == true)
          this.messageService.msg('Actualizado correctamente.', 'Cerrar');
      });
    this.dialogRef.close();
  }

  private register() {
    this.centerService.new(this.center).subscribe((data) => {
      if (data.status == true)
        this.messageService.msg('Creado correctamente.', 'Cerrar');
    });
    this.dialogRef.close();
  }
}
