import { Component, OnInit } from '@angular/core';
import { MatDialog } from '@angular/material/dialog';
import { Router } from '@angular/router';
import { Post } from 'src/app/models/post';
import { Veterinarian } from 'src/app/models/veterinarian';
import { InformationService } from 'src/app/services/information-service.service';
import { MessageService } from 'src/app/services/message.service';
import { PostService } from 'src/app/services/post.service';
import { VeterinaryService } from 'src/app/services/veterinary.service';
import { PostNewComponent } from '../post-new/post-new.component';

@Component({
  selector: 'app-post-details',
  templateUrl: './post-details.component.html',
  styleUrls: ['./post-details.component.css'],
})
export class PostDetailsComponent implements OnInit {
  post!: Post;
  veterinary!: Veterinarian;
  isVet: boolean = false;
  constructor(
    private postService: PostService,
    private info: InformationService,
    private vetService: VeterinaryService,
    private messageService: MessageService,
    private dialog: MatDialog,
    private router: Router
  ) {}

  ngOnInit() {
    this.load();
  }

  load(): void {
    this.postService.getPostbyID(this.info.getPostId()).subscribe((data) => {
      this.post = data;
      this.vetService.getbyID(this.post.vet_id).subscribe((r) => {
        this.veterinary = r;
      });
      if (
        this.info.getType() == 'vet' &&
        this.post.vet_id == this.info.getuser()
      ) {
        this.isVet = true;
      }
    });
  }

  delete(id: number) {
    this.postService.remove(id).subscribe((r) => {
      this.messageService.msg(r.response, 'Cerrar');
      this.router.navigateByUrl('post');
    });
  }

  edit(ID: number) {
    let d = this.dialog.open(PostNewComponent, { width: '70%' });
    d.afterClosed().subscribe(() => this.load());
  }

  like() {
    this.votes(1);
  }
  dislike() {
    this.votes(-1);
  }

  votes(num: number) {
    this.postService
      .valoration(this.post.post_id, num)
      .subscribe(() => this.load());
  }
}
