import { HttpClient } from '@angular/common/http';
import { EventEmitter, Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { AuthDTO } from '../models/auth';
import { LogClass } from '../models/login.dto';
import { ResponseAPI } from '../models/responseAPI';
import { UserInfo } from '../models/user.dto';
@Injectable({
  providedIn: 'root',
})
export class UserService {
  private UserURL: string;

  authChange: EventEmitter<boolean> = new EventEmitter<boolean>();

  constructor(private http: HttpClient) {
    this.UserURL = 'http://54.216.130.53/user/';
  }

  register(user: UserInfo): Observable<ResponseAPI> {
    return this.http.post<ResponseAPI>(this.UserURL, user);
  }

  login(user: LogClass): Observable<AuthDTO> {
    return this.http.post<AuthDTO>(
      this.UserURL +
        'login?user_email=' +
        user.email +
        '&user_pass=' +
        user.password,
      ''
    );
  }

  getUser(id: number): Observable<UserInfo> {
    return this.http.get<UserInfo>(this.UserURL + id);
  }

  logout(): void {
    this.authChange.emit(false);
    localStorage.removeItem('token');
    localStorage.removeItem('user_id');
    localStorage.removeItem('type');
  }
  isAuth(): boolean {
    let auth = localStorage.getItem('token') !== null;
    if (auth) this.authChange.emit(true);
    return auth;
  }

  update(id: number, data: UserInfo): Observable<ResponseAPI> {
    return this.http.patch<ResponseAPI>(this.UserURL + id, data);
  }
}
