import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { Center } from '../models/center';
import { ResponseAPI } from '../models/responseAPI';

@Injectable({
  providedIn: 'root',
})
export class CenterService {
  private URL: string;

  constructor(private http: HttpClient) {
    this.URL = 'http://54.216.130.53/center/';
  }

  new(center: Center): Observable<ResponseAPI> {
    return this.http.post<ResponseAPI>(this.URL, center);
  }
  getbyID(center_id: number): Observable<Center> {
    return this.http.get<Center>(this.URL + center_id);
  }
  getAll(): Observable<Center[]> {
    return this.http.get<Center[]>(this.URL);
  }
  updateCenter(center_id: number, center: Center): Observable<ResponseAPI> {
    return this.http.patch<ResponseAPI>(this.URL + center_id, center);
  }
  delete(center_id: number): Observable<ResponseAPI> {
    return this.http.delete<ResponseAPI>(this.URL + '?center_id=' + center_id);
  }

  valoration(center_id: number, votes: number): Observable<ResponseAPI> {
    return this.http.patch<ResponseAPI>(
      this.URL + 'val/' + center_id + '?votes=' + votes,
      ''
    );
  }
}
