import { Component, Input, OnInit, Optional } from '@angular/core';
import {
  FormBuilder,
  FormControl,
  FormGroup,
  Validators,
} from '@angular/forms';
import { MatDialogRef } from '@angular/material/dialog';
import { HistoryInfo } from 'src/app/models/history.dto';
import { HistoryService } from 'src/app/services/history.service';
import { InformationService } from 'src/app/services/information-service.service';
import { MessageService } from 'src/app/services/message.service';

@Component({
  selector: 'app-history-new',
  templateUrl: './history-new.component.html',
  styleUrls: ['./history-new.component.css'],
})
export class HistoryNewComponent implements OnInit {
  isUpdateMode: boolean;
  isVet: boolean = false;

  history: HistoryInfo;
  illness: FormControl;
  medical_examination: FormControl;
  prognosis: FormControl;
  coments: FormControl;
  @Input() history_id!: string;
  motive: FormControl;
  diagnostic: FormControl;
  treatment: FormControl;

  historyForm: FormGroup;
  valid: boolean | null;

  constructor(
    private formBuilder: FormBuilder,
    private historyService: HistoryService,
    private messageService: MessageService,
    private info: InformationService,
    @Optional() public dialogRef: MatDialogRef<HistoryNewComponent>
  ) {
    this.isUpdateMode = false;
    this.history = new HistoryInfo('', '', '', '', -1, '', '', '', -1);
    this.valid = null;
    this.illness = new FormControl(this.history.illness);
    this.medical_examination = new FormControl(
      this.history.medical_examination,
      [Validators.required]
    );
    this.prognosis = new FormControl(this.history.prognosis);
    this.coments = new FormControl(this.history.coments);
    this.motive = new FormControl(this.history.motive, [Validators.required]);
    this.diagnostic = new FormControl(this.history.diagnostic, [
      Validators.required,
    ]);
    this.treatment = new FormControl(this.history.treatment);

    this.historyForm = this.formBuilder.group({
      illness: this.illness,
      medical_examination: this.medical_examination,
      prognosis: this.prognosis,
      coments: this.coments,
      motive: this.motive,
      diagnostic: this.diagnostic,
      treatment: this.treatment,
      pet_id: this.info.getPetId(),
      vet_id: this.info.getuser(),
    });
    if (this.info.getType() == 'vet') {
      this.isVet = true;
    } else {
      this.historyForm.disable();
    }
  }
  ngOnInit() {
    if (this.history_id) {
      this.isUpdateMode = true;
      this.historyService.getByID(parseInt(this.history_id)).subscribe((d) => {
        this.history = d;
        this.illness.setValue(this.history.illness),
          this.medical_examination.setValue(this.history.medical_examination),
          this.prognosis.setValue(this.history.prognosis);
        this.coments.setValue(this.history.coments),
          this.motive.setValue(this.history.motive),
          this.diagnostic.setValue(this.history.diagnostic);
        this.treatment.setValue(this.history.treatment);

        this.historyForm = this.formBuilder.group({
          pet_id: this.info.getPetId(),
          vet_id: this.info.getuser(),
          illness: this.illness,
          medical_examination: this.medical_examination,
          prognosis: this.prognosis,
          coments: this.coments,
          motive: this.motive,
          diagnostic: this.diagnostic,
          treatment: this.treatment,
        });
      });
    }
  }

  async save(): Promise<void> {
    this.valid = false;
    if (this.historyForm.invalid) {
      return;
    }
    this.valid = true;
    this.history = this.historyForm.value;

    if (this.isUpdateMode) {
      this.update();
    } else {
      this.register();
    }
  }

  private async update() {
    this.historyService
      .update(parseInt(this.history_id), this.history)
      .subscribe((data) => {
        if (data.status === true)
          this.messageService.msg('Historia clinica actualizada.', 'Cerrar');
        else {
          this.messageService.msg(
            'Error al actualizar, intentalo de nuevo.',
            'Cerrar'
          );
        }
      });
    this.dialogRef.close(true);
  }

  private register() {
    this.historyService.new(this.history).subscribe((data) => {
      if (data.status === true)
        this.messageService.msg('Historia clinica aadida.', 'Cerrar');
      else {
        this.messageService.msg(
          'Error al aadir, intentalo de nuevo.',
          'Cerrar'
        );
      }
    });
    this.dialogRef.close(true);
  }
}
