import { DatePipe } from '@angular/common';
import { Component, OnInit, Optional } from '@angular/core';
import {
  FormBuilder,
  FormControl,
  FormGroup,
  Validators,
} from '@angular/forms';
import { MatDialogRef } from '@angular/material/dialog';
import { Router } from '@angular/router';
import { UserInfo } from 'src/app/models/user.dto';
import { InformationService } from 'src/app/services/information-service.service';
import { MessageService } from 'src/app/services/message.service';
import { UserService } from 'src/app/services/user.service';
@Component({
  selector: 'app-register',
  templateUrl: './register.component.html',
  styleUrls: ['./register.component.css'],
})
export class RegisterComponent implements OnInit {
  registerUser: UserInfo;
  hide = true;
  user_name: FormControl;
  user_lastname: FormControl;
  user_email: FormControl;
  user_pass: FormControl;
  user_birthdate: FormControl;

  registerForm: FormGroup;
  valid: boolean | null;
  isUpdateMode: boolean = false;

  constructor(
    private formBuilder: FormBuilder,
    private userService: UserService,
    private datePipe: DatePipe,
    private messageService: MessageService,
    private router: Router,
    private info: InformationService,
    @Optional() public dialogRef: MatDialogRef<RegisterComponent>
  ) {
    this.registerUser = new UserInfo('', '', '', '', '', new Date());
    this.valid = null;
    this.user_name = new FormControl(this.registerUser.user_name, [
      Validators.required,
    ]);
    this.user_lastname = new FormControl(this.registerUser.user_lastname, [
      Validators.required,
    ]);
    this.user_email = new FormControl(this.registerUser.user_email, [
      Validators.required,
      Validators.email,
    ]);
    this.user_pass = new FormControl(this.registerUser.user_pass, [
      Validators.required,
      Validators.minLength(6),
    ]);

    this.user_birthdate = new FormControl(this.registerUser.user_birthdate, [
      Validators.required,
    ]);

    this.registerForm = this.formBuilder.group({
      user_name: this.user_name,
      user_lastname: this.user_lastname,
      user_email: this.user_email,
      user_pass: this.user_pass,
      user_birthdate: this.datePipe.transform(
        this.user_birthdate.value,
        "yyyy-MM-dd'T'HH:mm:ss.SSS'Z'"
      ),
    });
  }

  ngOnInit(): void {
    const userID = this.info.getuser();
    if (userID > -1) {
      this.isUpdateMode = true;
      this.userService.getUser(userID).subscribe((d) => {
        this.registerUser = d;
        this.user_name.setValue(this.registerUser.user_name);
        this.user_lastname.setValue(this.registerUser.user_lastname);
        this.user_birthdate.setValue(this.registerUser.user_birthdate);
        this.user_email.setValue(this.registerUser.user_email);
        this.user_pass.setValue(this.registerUser.user_pass);

        this.registerForm = this.formBuilder.group({
          user_id: userID,
          user_name: this.user_name,
          user_lastname: this.user_lastname,
          user_birthdate: this.user_birthdate,
          user_email: this.user_email,
          user_pass: this.user_pass,
        });
      });
    }
  }

  async save(): Promise<void> {
    if (this.registerForm.invalid) {
      return;
    }
    this.registerUser = this.registerForm.value;
    const userID = this.info.getuser();
    console.log(userID);
    if (userID > -1) {
      this.update(userID);
    } else {
      this.new();
    }
  }

  private new() {
    this.userService.register(this.registerUser).subscribe((r) => {
      this.messageService.msg(r.response, 'Cerrar');
      if (r.status == true) {
        this.router.navigateByUrl('login');
      }
    });
    this.dialogRef.close();
  }

  private update(userID: number) {
    this.userService.update(userID, this.registerUser).subscribe((r) => {
      this.messageService.msg(r.response, 'Cerrar');
    });
    this.dialogRef.close();
  }
}
