import { HttpClient } from '@angular/common/http';
import { EventEmitter, Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { AuthDTO } from '../models/auth';
import { LogClass } from '../models/login.dto';
import { ResponseAPI } from '../models/responseAPI';
import { Veterinarian } from '../models/veterinarian';

@Injectable({
  providedIn: 'root',
})
export class VeterinaryService {
  private URL: string;
  authChange: EventEmitter<boolean> = new EventEmitter<boolean>();

  constructor(private http: HttpClient) {
    this.URL = 'http://54.216.130.53/vet/';
  }

  new(vet: Veterinarian): Observable<ResponseAPI> {
    return this.http.post<ResponseAPI>(this.URL, vet);
  }
  getbyID(vet_id: number): Observable<Veterinarian> {
    return this.http.get<Veterinarian>(this.URL + vet_id);
  }
  getVets(): Observable<Veterinarian[]> {
    return this.http.get<Veterinarian[]>(this.URL);
  }
  updateVet(vet_id: number, vet: Veterinarian): Observable<ResponseAPI> {
    return this.http.patch<ResponseAPI>(this.URL + vet_id, vet);
  }
  delete(vet_id: number): Observable<ResponseAPI> {
    return this.http.delete<ResponseAPI>(this.URL + '?vet_id=' + vet_id);
  }

  login(vet: LogClass): Observable<AuthDTO> {
    return this.http.post<AuthDTO>(
      this.URL + 'login?vet_email=' + vet.email + '&vet_pass=' + vet.password,
      ''
    );
  }

  aceptJoin(vet_id: number): Observable<ResponseAPI> {
    const body = {
      vet_status: true,
    };
    return this.http.patch<ResponseAPI>(this.URL + vet_id, body);
  }

  logout(): void {
    this.authChange.emit(false);
    localStorage.removeItem('token');
  }
  isAuth(): boolean {
    let auth = localStorage.getItem('token') !== null;
    if (auth) this.authChange.emit(true);
    return auth;
  }
}
