import { Component, Inject, Input, OnInit, Optional } from '@angular/core';
import {
  FormBuilder,
  FormControl,
  FormGroup,
  Validators,
} from '@angular/forms';
import { MAT_DIALOG_DATA, MatDialogRef } from '@angular/material/dialog';
import { MessageVet } from 'src/app/models/message.dto';
import { InformationService } from 'src/app/services/information-service.service';
import { MessageVetService } from 'src/app/services/message-vet.service';
import { MessageService } from 'src/app/services/message.service';

@Component({
  selector: 'app-message-vet-new',
  templateUrl: './message-vet-new.component.html',
  styleUrls: ['./message-vet-new.component.css'],
})
export class MessageVetNewComponent implements OnInit {
  messageVetForm: FormGroup;
  messageVet: MessageVet;
  center_id!: number;
  valid: boolean | null;
  message_title: FormControl;
  message_body: FormControl;
  message_response: FormControl;
  vetId!: number;
  @Input() centerId!: number;
  @Input() petId!: number;
  @Input() msgId!: number;
  isUpdateMode: boolean;
  canSave: boolean = true;
  isVet: boolean = false;

  constructor(
    private formBuilder: FormBuilder,
    private messagevetservice: MessageVetService,
    private messageService: MessageService,
    private info: InformationService,
    @Inject(MAT_DIALOG_DATA) public data: any,
    @Optional() public dialogRef: MatDialogRef<MessageVetNewComponent>
  ) {
    if (this.info.getType() == 'vet') {
      this.isVet = true;
    }
    this.msgId = data.msgId;
    this.center_id = parseInt(data.centerId);
    this.petId = parseInt(data.petId);

    this.isUpdateMode = false;
    this.messageVet = new MessageVet('', '', 0, 0, 0, new Date());
    this.valid = null;
    this.message_title = new FormControl(this.messageVet.message_title, [
      Validators.required,
    ]);

    this.message_body = new FormControl(this.messageVet.message_body, [
      Validators.required,
    ]);
    this.message_response = new FormControl(this.messageVet.message_response);

    this.messageVetForm = this.formBuilder.group({
      message_body: this.message_body,
      message_title: this.message_title,
      pet_id: this.petId,
      center_id: this.center_id,
    });
  }

  ngOnInit() {
    if (this.msgId) {
      this.messagevetservice.getByID(this.msgId).subscribe((d) => {
        this.messageVet = d;

        this.message_title.setValue(this.messageVet.message_title);
        this.message_body.setValue(this.messageVet.message_body);
        this.message_response.setValue(this.messageVet.message_response);

        this.message_title.disable();
        this.message_body.disable();

        if (this.messageVet.vet_id) {
          this.message_response.disable();
          this.canSave = false;
        }
        if (!this.isVet) {
          this.message_response.disable();
          this.canSave = false;
        }

        this.isUpdateMode = true;
        this.messageVetForm = this.formBuilder.group({
          vet_id: this.info.getuser(),
          center_id: this.center_id,
          message_title: this.message_title,
          message_body: this.message_body,
          message_response: this.message_response,
        });
      });
    }
  }
  async save(): Promise<void> {
    this.valid = false;
    if (this.messageVetForm.invalid) {
      return;
    }
    this.valid = true;
    this.messageVet = this.messageVetForm.value;

    if (this.isUpdateMode) {
      this.update();
    } else {
      this.new();
    }
  }

  new() {
    if (this.center_id) {
      this.messageVet.center_id = this.center_id;
      this.messagevetservice.newMessage(this.messageVet).subscribe((r) => {
        if (r.status === true) {
          this.messageService.msg('Mensaje enviado.', 'Cerrar');
        } else {
          this.messageService.msg(
            'Error al enviar el mensaje, intentalo de nuevo.',
            'Cerrar'
          );
        }
      });
    } else {
      this.messageService.msg(
        'Error al enviar el mensaje, primero se tiene que asociar la mascota a un centro.',
        'Cerrar'
      );
    }
    this.dialogRef.close(true);
  }

  update() {
    if (this.msgId) {
      this.messagevetservice
        .updateMessage(this.msgId, this.messageVet)
        .subscribe((d) => {
          if (d.status === true) {
            this.messageService.msg('Mensaje actualizado.', 'Cerrar');
          } else {
            this.messageService.msg(
              'Error al actualizar el mensaje, intentalo de nuevo.',
              'Cerrar'
            );
          }
        });
    }
    this.dialogRef.close(true);
  }
}
