import { Component, OnInit } from '@angular/core';
import { MatDialog } from '@angular/material/dialog';
import { ActivatedRoute } from '@angular/router';
import { Center } from 'src/app/models/center';
import { Veterinarian } from 'src/app/models/veterinarian';
import { CenterService } from 'src/app/services/center.service';
import { InformationService } from 'src/app/services/information-service.service';
import { VeterinaryService } from 'src/app/services/veterinary.service';
import { CenterRegisterComponent } from '../center-register/center-register.component';

@Component({
  selector: 'app-center-details',
  templateUrl: './center-details.component.html',
  styleUrls: ['./center-details.component.css'],
})
export class CenterDetailsComponent implements OnInit {
  vet: Veterinarian[] = [];
  centerId!: number;
  center!: Center;
  viewList: boolean = false;
  canEdit: boolean = false;
  constructor(
    private route: ActivatedRoute,
    private vetService: VeterinaryService,
    private centerService: CenterService,
    private dialog: MatDialog,
    private info: InformationService
  ) {}

  ngOnInit(): void {
    this.route.params.subscribe((params) => {
      this.centerId = params['centerId'];
      this.load();
    });
  }

  async load() {
    this.vetService.getVets().subscribe((d) => {
      this.vet = d.filter(
        (vet) =>
          vet.center_id?.toString() === this.centerId.toString() &&
          vet.vet_status == true
      );
      if (this.vet.length > 0) this.viewList = true;
    });
    this.centerService.getbyID(this.centerId).subscribe((d) => {
      this.center = d;
      if (this.info.getType() == 'vet') {
        this.vetService.getbyID(this.info.getuser()).subscribe((v) => {
          if (v.vet_email == this.center.email_admin) {
            this.canEdit = true;
          }
        });
      } else {
        this.canEdit = false;
      }
    });
  }
  like() {
    this.votes(1);
  }
  dislike() {
    this.votes(-1);
  }

  votes(num: number) {
    this.centerService
      .valoration(this.centerId, num)
      .subscribe(() => this.load());
  }

  editCenter(id: number) {
    let dialogRef = this.dialog.open(CenterRegisterComponent, {
      width: '70%',
      data: { centerId: id },
    });
    dialogRef.afterClosed().subscribe(() => {
      this.load();
    });
  }
}
