export class LogClass {
  user_id: string;
  access_token: string;
  email: string;
  password: string;
  user_type!: string;

  constructor(
    user_id: string,
    access_token: string,
    email: string,
    password: string
  ) {
    this.user_id = user_id;
    this.access_token = access_token;
    this.email = email;
    this.password = password;
  }
}
