import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { MessageVet } from '../models/message.dto';
import { ResponseAPI } from '../models/responseAPI';

@Injectable({
  providedIn: 'root',
})
export class MessageVetService {
  private URL: string;

  constructor(private http: HttpClient) {
    this.URL = 'http://54.216.130.53/message/';
  }

  newMessage(msg: MessageVet): Observable<ResponseAPI> {
    return this.http.post<ResponseAPI>(this.URL, msg);
  }

  getMessages(pet_id: number): Observable<MessageVet[]> {
    return this.http.get<MessageVet[]>(this.URL + '?pet_id=' + pet_id);
  }

  deleteMessage(msg_id: number): Observable<ResponseAPI> {
    return this.http.delete<ResponseAPI>(this.URL + '?msg_id=' + msg_id);
  }

  updateMessage(msg_id: number, message: MessageVet): Observable<ResponseAPI> {
    return this.http.patch<ResponseAPI>(this.URL + msg_id, message);
  }
  getByID(msg_id: number): Observable<MessageVet> {
    return this.http.get<MessageVet>(this.URL + msg_id);
  }
}
