import { formatDate } from '@angular/common';
import { Component, OnInit, Optional } from '@angular/core';
import {
  FormBuilder,
  FormControl,
  FormGroup,
  Validators,
} from '@angular/forms';
import { MatDialogRef } from '@angular/material/dialog';
import { PetInfo } from 'src/app/models/pet.dto';
import { InformationService } from 'src/app/services/information-service.service';
import { MessageService } from 'src/app/services/message.service';
import { PetService } from 'src/app/services/pet.service';

interface IOptions {
  value: string;
  description: string;
}
@Component({
  selector: 'app-pet-register',
  templateUrl: './pet-register.component.html',
  styleUrls: ['./pet-register.component.css'],
})
export class PetRegisterComponent implements OnInit {
  Especie!: string[];
  Raza!: string[];
  isUpdateMode: boolean;
  pet: PetInfo;
  user_id: FormControl;
  pet_name: FormControl;
  pet_chip: FormControl;
  pet_specie: FormControl;
  pet_race: FormControl;
  pet_sex: FormControl;
  pet_birthdate: FormControl;

  pet_img: FormControl;
  pet_siteimplantation: FormControl;
  pet_dateimplantation: FormControl;
  pet_color: FormControl;
  pet_characteristics: FormControl;
  registerPetForm: FormGroup;
  valid: boolean | null;
  constructor(
    private formBuilder: FormBuilder,
    private petService: PetService,
    private messageService: MessageService,
    private infoPet: InformationService,
    @Optional() public dialogRef: MatDialogRef<PetRegisterComponent>
  ) {
    this.isUpdateMode = false;
    this.pet = new PetInfo(
      -1,
      '',
      '',
      '',
      '',
      '',
      new Date(),
      '',
      new Date(),
      '',
      ''
    );

    this.Especie = this.petService.getEspecie();
    this.Raza = this.petService.getRaza();

    this.valid = null;
    this.user_id = new FormControl(this.pet.user_id, [Validators.required]);
    this.pet_name = new FormControl(this.pet.pet_name, [Validators.required]);
    this.pet_chip = new FormControl(this.pet.pet_chip);
    this.pet_specie = new FormControl(this.pet.pet_specie);
    this.pet_race = new FormControl(this.pet.pet_race);
    this.pet_sex = new FormControl(this.pet.pet_sex);
    this.pet_img = new FormControl(this.pet.pet_img);
    this.pet_birthdate = new FormControl(this.pet.pet_birthdate);
    this.pet_siteimplantation = new FormControl(this.pet.pet_siteimplantation);
    this.pet_dateimplantation = new FormControl(this.pet.pet_dateimplantation);
    this.pet_color = new FormControl(this.pet.pet_color);
    this.pet_characteristics = new FormControl(this.pet.pet_characteristics);

    this.registerPetForm = this.formBuilder.group({
      user_id: this.user_id,
      pet_name: this.pet_name,
      pet_chip: this.pet_chip,
      pet_specie: this.pet_specie,
      pet_race: this.pet_race,
      pet_sex: this.pet_sex,
      pet_birthdate: this.pet_birthdate,
      pet_siteimplantation: this.pet_siteimplantation,
      pet_dateimplantation: this.pet_dateimplantation,
      pet_color: this.pet_color,
      pet_characteristics: this.pet_characteristics,
      pet_img: this.pet_img,
    });
  }
  ngOnInit() {
    const petId = this.infoPet.getPetId();
    if (petId > -1) {
      this.isUpdateMode = true;
      this.petService.getPetbyID(petId).subscribe((d) => {
        this.pet = d;
        this.user_id.setValue(this.pet.user_id),
          this.pet_name.setValue(this.pet.pet_name),
          this.pet_chip.setValue(this.pet.pet_chip);
        this.pet_specie.setValue(this.pet.pet_specie),
          this.pet_race.setValue(this.pet.pet_race),
          this.pet_sex.setValue(this.pet.pet_sex);
        this.pet_img.setValue(this.pet.pet_img);
        if (this.pet.pet_birthdate)
          this.pet_birthdate.setValue(
            formatDate(this.pet.pet_birthdate, 'yyyy-MM-dd', 'en')
          );
        this.pet_siteimplantation.setValue(this.pet.pet_siteimplantation);
        if (this.pet.pet_dateimplantation)
          this.pet_dateimplantation.setValue(
            formatDate(this.pet.pet_dateimplantation, 'yyyy-MM-dd', 'en')
          );
        this.pet_color.setValue(this.pet.pet_color);
        this.pet_characteristics.setValue(this.pet.pet_characteristics);

        this.registerPetForm = this.formBuilder.group({
          pet_id: petId,
          user_id: this.user_id,
          pet_name: this.pet_name,
          pet_chip: this.pet_chip,
          pet_specie: this.pet_specie,
          pet_race: this.pet_race,
          pet_sex: this.pet_sex,
          pet_birthdate: this.pet_birthdate,
          pet_siteimplantation: this.pet_siteimplantation,
          pet_dateimplantation: this.pet_dateimplantation,
          pet_color: this.pet_color,
          pet_img: this.pet_img,
          pet_characteristics: this.pet_characteristics,
        });
      });
    }
  }

  async save(): Promise<void> {
    this.valid = false;
    if (this.registerPetForm.invalid) {
      return;
    }
    this.valid = true;
    this.pet = this.registerPetForm.value;
    const userId = localStorage.getItem('user_id');

    if (userId) {
      this.pet.user_id = parseInt(userId);
      if (this.isUpdateMode) {
        this.update();
      } else {
        this.register();
      }
    }
  }

  private update() {
    this.petService
      .updatePet(this.infoPet.getPetId(), this.pet, this.pet.user_id)
      .subscribe((data) => {
        if (data.status === true)
          this.messageService.msg('Mascota actualizada.', 'Cerrar');
        else {
          this.messageService.msg(
            'Error al actualizar, intentalo de nuevo.',
            'Cerrar'
          );
        }
      });
    this.dialogRef.close(true);
  }

  private register() {
    this.petService.register(this.pet).subscribe((data) => {
      if (data.status === true)
        this.messageService.msg('Mascota aadida.', 'Cerrar');
      else {
        this.messageService.msg(
          'Error al aadir, intentalo de nuevo.',
          'Cerrar'
        );
      }
    });
    this.dialogRef.close(true);
  }
}
