export class Veterinarian {
  vet_id!: number;
  vet_email: string;
  vet_dni: string;
  vet_photo: string;
  vet_information: string;
  modify!: Date;
  vet_pass: string;
  vet_name: string;
  vet_status!: boolean;
  creation!: Date;
  center_id: number | null;

  constructor(
    vet_email: string,
    vet_dni: string,
    vet_photo: string,
    vet_information: string,
    vet_pass: string,
    vet_name: string,
    center_id: number | null
  ) {
    this.vet_email = vet_email;
    this.vet_dni = vet_dni;
    this.vet_photo = vet_photo;
    this.vet_information = vet_information;
    this.vet_pass = vet_pass;
    this.vet_name = vet_name;
    this.center_id = center_id;
  }
}
