import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { HistoryInfo } from '../models/history.dto';
import { ResponseAPI } from '../models/responseAPI';

@Injectable({
  providedIn: 'root',
})
export class HistoryService {
  private URL: string;

  constructor(private http: HttpClient) {
    this.URL = 'http://54.216.130.53/history/';
  }

  getAllHistory(pet_id: number): Observable<HistoryInfo[]> {
    return this.http.get<HistoryInfo[]>(this.URL + '?pet_id=' + pet_id);
  }

  new(hist: HistoryInfo): Observable<ResponseAPI> {
    return this.http.post<ResponseAPI>(this.URL, hist);
  }

  getByID(history_id: number): Observable<HistoryInfo> {
    return this.http.get<HistoryInfo>(this.URL + history_id);
  }

  update(history_id: number, history: HistoryInfo): Observable<ResponseAPI> {
    return this.http.patch<ResponseAPI>(this.URL + history_id, history);
  }

  delete(history_id: number): Observable<ResponseAPI> {
    return this.http.delete<ResponseAPI>(this.URL + history_id);
  }
}
