import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { AboutUsComponent } from './components/about-us/about-us.component';
import { CenterDetailsComponent } from './components/center/center-details/center-details.component';
import { CenterMainComponent } from './components/center/center-main/center-main.component';
import { HomeComponent } from './components/home/home.component';
import { LoginComponent } from './components/login/login.component';
import { PetDetailsComponent } from './components/pets/pet-details/pet-details.component';
import { PetListComponent } from './components/pets/pet-list/pet-list.component';
import { PetRegisterComponent } from './components/pets/pet-register/pet-register.component';
import { PostDetailsComponent } from './components/post/post-details/post-details.component';
import { PostListComponent } from './components/post/post-list/post-list.component';
import { PrincipalComponent } from './components/principal/principal.component';
import { NewUserComponent } from './components/users/new-user/new-user.component';
import { AuthGuard } from './guard/auth.guard';
const routes: Routes = [
  { path: '', component: HomeComponent },
  { path: 'user-register', component: NewUserComponent },
  {
    path: 'pet-register',
    component: PetRegisterComponent,
    canActivate: [AuthGuard],
  },
  { path: 'login', component: LoginComponent },
  { path: 'pet-list', component: PetListComponent, canActivate: [AuthGuard] },
  {
    path: 'pets-info/:petId',
    component: PetDetailsComponent,
    canActivate: [AuthGuard],
  },
  { path: 'post', component: PostListComponent, canActivate: [AuthGuard] },
  {
    path: 'post/:postId',
    component: PostDetailsComponent,
    canActivate: [AuthGuard],
  },
  { path: 'vet', component: CenterMainComponent, canActivate: [AuthGuard] },
  {
    path: 'center/:centerId',
    component: CenterDetailsComponent,
    canActivate: [AuthGuard],
  },
  {
    path: 'aboutUs',
    component: AboutUsComponent,
  },
  { path: 'init', component: PrincipalComponent, canActivate: [AuthGuard] },
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule],
})
export class AppRoutingModule {}
