import { Location } from '@angular/common';
import { Component, Input, OnInit } from '@angular/core';
import { MatDialog } from '@angular/material/dialog';
import { HistoryInfo } from 'src/app/models/history.dto';
import { HistoryService } from 'src/app/services/history.service';
import { InformationService } from 'src/app/services/information-service.service';
import { MessageService } from 'src/app/services/message.service';
import { HistoryNewComponent } from '../history-new/history-new.component';

@Component({
  selector: 'app-history-list',
  templateUrl: './history-list.component.html',
  styleUrls: ['./history-list.component.css'],
})
export class HistoryListComponent implements OnInit{
  history!: HistoryInfo[];
  @Input() petId!: string;
  isVet: boolean = false;

  constructor(
    private historyService: HistoryService,
    private dialog: MatDialog,
    private info: InformationService,
    private messageService: MessageService,
    private location: Location
  ) {
    if (this.info.getType() == 'vet') this.isVet = true;
  }
  ngOnInit(): void {
    this.load();
  }

  load(): void {
    this.historyService.getAllHistory(parseInt(this.petId)).subscribe((r) => {
      if (r.length > 0) this.history = r;
    });
  }

  new() {
    let diag = this.dialog.open(HistoryNewComponent, {
      width: '70%',
      panelClass: 'dialog-button',
    });
    diag.afterClosed().subscribe(() => this.load());
  }
  async delete(id: number) {
    this.historyService.delete(id).subscribe((r) => {
      this.messageService.msg(r.response, 'Cerrar');
      this.location.back();
    });
  }
}
