export class PetInfo {
  pet_id!: number;
  user_id: number;
  pet_name: string;
  pet_chip?: string;
  pet_specie?: string;
  pet_race?: string;
  pet_sex?: string;
  center_id?: number;
  pet_birthdate?: Date;
  pet_siteimplantation?: string;
  pet_dateimplantation?: Date;
  pet_color?: string;
  pet_characteristics?: string;
  pet_img?: string;

  constructor(
    user_id: number,
    pet_name: string,
    pet_chip: string,
    pet_specie: string,
    pet_race: string,
    pet_sex: string,
    pet_birthdate: Date,
    pet_siteimplantation: string,
    pet_dateimplantation: Date,
    pet_color: string,
    pet_characteristics: string
  ) {
    this.user_id = user_id;
    this.pet_name = pet_name;
    this.pet_chip = pet_chip;
    this.pet_specie = pet_specie;
    this.pet_race = pet_race;
    this.pet_sex = pet_sex;
    this.pet_birthdate = pet_birthdate;
    this.pet_siteimplantation = pet_siteimplantation;
    this.pet_dateimplantation = pet_dateimplantation;
    this.pet_color = pet_color;
    this.pet_characteristics = pet_characteristics;
  }
}
