import { Component, Input, OnInit } from '@angular/core';
import { MatDialog } from '@angular/material/dialog';
import { MessageVet } from 'src/app/models/message.dto';
import { InformationService } from 'src/app/services/information-service.service';
import { MessageVetService } from 'src/app/services/message-vet.service';
import { MessageVetNewComponent } from '../message-vet-new/message-vet-new.component';
@Component({
  selector: 'app-message-vet-list',
  templateUrl: './message-vet-list.component.html',
  styleUrls: ['./message-vet-list.component.css'],
})
export class MessageVetListComponent implements OnInit {
  messages!: MessageVet[];
  @Input() petId!: string;
  @Input() centerId!: string;
  isVet: boolean = false;

  constructor(
    private messageVetService: MessageVetService,
    private dialog: MatDialog,
    private info: InformationService
  ) {
    if (this.info.getType() == 'vet') {
      this.isVet = true;
    }
  }

  ngOnInit() {
    this.load();
  }

  load(): void {
    this.messageVetService.getMessages(this.info.getPetId()).subscribe((r) => {
      this.messages = r;
    });
  }
  newMessage() {
    let d = this.dialog.open(MessageVetNewComponent, {
      width: '70%',
      data: { centerId: this.centerId, petId: this.petId },
    });
    d.afterClosed().subscribe(() => this.load());
  }

  viewMessage(message_id: number) {
    let d = this.dialog.open(MessageVetNewComponent, {
      width: '70%',
      data: { centerId: this.centerId, petId: this.petId, msgId: message_id },
    });
    d.afterClosed().subscribe(() => this.load());
  }
}
