import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { Activity } from '../models/activity';
import { ResponseAPI } from '../models/responseAPI';

@Injectable({
  providedIn: 'root',
})
export class ActivityService {
  private URL: string;
  tipos: string[] = ['Actividad fsica', 'Comida'];

  constructor(private http: HttpClient) {
    this.URL = 'http://54.216.130.53/activities/';
  }

  getTipos() {
    return this.tipos;
  }

  getActivities(pet_id: number): Observable<Activity[]> {
    return this.http.get<Activity[]>(this.URL + '?pet_id=' + pet_id);
  }

  new(activity: Activity): Observable<ResponseAPI> {
    return this.http.post<ResponseAPI>(this.URL, activity);
  }
  getbyID(act_id: number): Observable<Activity> {
    return this.http.get<Activity>(this.URL + act_id);
  }
  remove(act_id: number): Observable<ResponseAPI> {
    return this.http.delete<ResponseAPI>(this.URL + '?activity_id=' + act_id);
  }
  update(act_id: number, activity: Activity): Observable<ResponseAPI> {
    return this.http.patch<ResponseAPI>(this.URL + act_id, activity);
  }
}
