import { Component, OnInit, Optional } from '@angular/core';
import {
  FormBuilder,
  FormControl,
  FormGroup,
  Validators,
} from '@angular/forms';
import { MatDialogRef } from '@angular/material/dialog';
import { Router } from '@angular/router';
import { Center } from 'src/app/models/center';
import { Veterinarian } from 'src/app/models/veterinarian';
import { CenterService } from 'src/app/services/center.service';
import { InformationService } from 'src/app/services/information-service.service';
import { MessageService } from 'src/app/services/message.service';
import { VeterinaryService } from 'src/app/services/veterinary.service';

@Component({
  selector: 'app-register-vet',
  templateUrl: './register-vet.component.html',
  styleUrls: ['./register-vet.component.css'],
})
export class RegisterVetComponent implements OnInit {
  registerVet: Veterinarian;
  hide = true;
  vet_name: FormControl;
  vet_email: FormControl;
  vet_pass: FormControl;
  vet_dni: FormControl;
  vet_photo: FormControl;
  vet_information: FormControl;
  center_id: FormControl;
  registerForm: FormGroup;
  valid: boolean | null;
  center!: Center[];
  isUpdateMode: boolean = false;

  constructor(
    private formBuilder: FormBuilder,
    private vetService: VeterinaryService,
    private messageService: MessageService,
    private centerService: CenterService,
    private router: Router,
    private info: InformationService,
    @Optional() public dialogRef: MatDialogRef<RegisterVetComponent>
  ) {
    this.registerVet = new Veterinarian('', '', '', '', '', '', -1);
    this.valid = null;
    this.vet_name = new FormControl(this.registerVet.vet_name, [
      Validators.required,
    ]);
    this.vet_dni = new FormControl(this.registerVet.vet_dni, [
      Validators.required,
    ]);
    this.vet_email = new FormControl(this.registerVet.vet_email, [
      Validators.required,
      Validators.email,
    ]);
    this.vet_information = new FormControl(this.registerVet.vet_information, [
      Validators.required,
    ]);

    this.vet_pass = new FormControl(this.registerVet.vet_pass, [
      Validators.required,
    ]);
    this.vet_photo = new FormControl(this.registerVet.vet_photo);
    this.center_id = new FormControl(this.registerVet.center_id, [
      Validators.required,
    ]);

    this.registerForm = this.formBuilder.group({
      vet_name: this.vet_name,
      vet_email: this.vet_email,
      vet_pass: this.vet_pass,
      vet_dni: this.vet_dni,
      vet_photo: this.vet_photo,
      vet_information: this.vet_information,
      center_id: this.center_id,
    });
  }

  ngOnInit(): void {
    this.centerService.getAll().subscribe((d) => {
      this.center = d;
    });
    const userID = this.info.getuser();
    if (userID > -1 && this.info.getType() == 'vet') {
      this.isUpdateMode = true;
      this.vetService.getbyID(userID).subscribe((d) => {
        this.registerVet = d;
        this.vet_name.setValue(this.registerVet.vet_name);
        this.vet_email.setValue(this.registerVet.vet_email);
        this.vet_pass.setValue(this.registerVet.vet_pass);
        this.vet_dni.setValue(this.registerVet.vet_dni);
        this.vet_photo.setValue(this.registerVet.vet_photo);
        this.vet_information.setValue(this.registerVet.vet_information);
        this.center_id.setValue(this.registerVet.center_id);
        this.registerForm = this.formBuilder.group({
          vet_photo: this.vet_photo,
          vet_name: this.vet_name,
          vet_email: this.vet_email,
          vet_pass: this.vet_pass,
          vet_dni: this.vet_dni,
          vet_information: this.vet_information,
          center_id: this.center_id,
        });
      });
    }
  }

  async save(): Promise<void> {
    if (this.registerForm.invalid) {
      return;
    }

    this.registerVet = this.registerForm.value;

    const userID = this.info.getuser();
    if (userID > -1 && this.info.getType() == 'vet') {
      this.update(userID);
    } else {
      this.new();
    }
  }
  private new() {
    this.vetService.new(this.registerVet).subscribe((r) => {
      if (r.status == true) {
        if(this.registerVet.center_id ==-1){
          this.vetService.aceptJoin(parseInt(r.response)).subscribe(()=>{
            this.messageService.msg("Usuario creado, puedes crear el centro desde 'Centros'", 'Cerrar')
          });
        }else{
          this.messageService.msg(
            'Usuario creado. Tu usuario estar deshabilitado hasta que se acepte tu solicitud.',
            'Cerrar'
          );
        }
        this.router.navigateByUrl('login');
      }
    });
    this.dialogRef.close(true);
  }
  private update(userID: number) {
    this.vetService.updateVet(userID, this.registerVet).subscribe((r) => {
      if (r.status == true) {
        this.messageService.msg('Usuario actualizado.', 'Cerrar');
      } else {
        this.messageService.msg('Error al actualizar.', 'Cerrar');
      }
    });
    this.dialogRef.close(true);
  }
}
