export class HistoryInfo {
  illness: string;
  medical_examination: string;
  prognosis: string;
  coments: string;
  modify!: Date;
  pet_id: number;
  motive: string;
  diagnostic: string;
  history_id!: number;
  treatment: string;
  creation!: Date;
  vet_id: number;

  constructor(
    illness: string,
    medical_examination: string,
    prognosis: string,
    coments: string,
    pet_id: number,
    motive: string,
    diagnostic: string,
    treatment: string,
    vet_id: number
  ) {
    this.illness = illness;
    this.medical_examination = medical_examination;
    this.prognosis = prognosis;
    this.coments = coments;
    this.pet_id = pet_id;
    this.motive = motive;
    this.diagnostic = diagnostic;
    this.treatment = treatment;
    this.vet_id = vet_id;
  }
}
